import React from "react";
import "./SidePanel.css";
import Picture from "./profilePicture.jpg";

class SidePanel extends React.Component {
  render() {
    return (
      <div className="fluid-container">
        <div className="row profilePic">
          <div className="col img-wrapper">
            <img id="pic" src={Picture} alt="Profile"></img>
          </div>
        </div>
        <div className="row info">
          <div className="col info-wrapper">
            <div className="row link">
              <div className="col">
                <a
                  className="btn btn-primary side-button d-flex h-80 align-items-center justify-content-center"
                  role="button"
                  href="https://www.linkedin.com/in/gia-bao-danny-nguyen"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <span>LinkedIn</span>
                </a>
              </div>
            </div>
            <div className="row link">
              <div className="col">
                <a
                  className="btn btn-primary side-button d-flex h-80 align-items-center justify-content-center"
                  role="button"
                  href="https://twitter.com/BaoGiaNguyen"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <span>Twitter</span>
                </a>
              </div>
            </div>
            <div className="row link">
              <div className="col">
                <a
                  className="btn btn-primary side-button d-flex h-80 align-items-center justify-content-center"
                  role="button"
                  target="_blank"
                  href="https://github.com/DannyNguyenCode"
                  rel="noopener noreferrer"
                >
                  <span>Github</span>
                </a>
              </div>
            </div>
            <div className="row link">
              <div className="col">
                <a
                  className="btn btn-primary side-button d-flex h-80 align-items-center justify-content-center"
                  role="button"
                  target="_blank"
                  href="https://dannynguyencode.github.io/"
                  rel="noopener noreferrer"
                >
                  <span>Resume</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SidePanel;
